.advantages__item::after {
  @media (min-width: $table-width) {
    content: "1";
    position: absolute;
    display: block;
    font-size: 228px;
    left: 126px;
    display: block;
    z-index: -2;
    color: $bgc;
  }
}

.advantages__item--powder::after {
  @media (min-width: $table-width) {
    content: "2";

  }
}

.advantages__item--food-substitute::after {
  @media (min-width: $table-width) {
    content: "3";
  }
}

.advantages__item--wight::after {
  @media (min-width: $table-width) {
    content: "4";

  }
}

.advantages__item::before {
  position: absolute;
  display: block;
  z-index: 1;
  content: "";
  width: 60px;
  background-color: $green;
  height: 60px;
}

.advantages__item--natural::before {
  background-image: url("../img/advantage-leaf@1x.png");
  background-repeat: no-repeat;
  background-position: center;
  top: 0;
  left: 0;
  background-size: 27px 30px;


  @media (min-width: $table-width) {
    top: 15px;
  }

  @media (min-width: $desktop-width) {
    top: 25px;
    left: 26px;
  }
}

.advantages__item--powder::before {
  background-image: url("../img/advantage-powder@1x.png");
  background-repeat: no-repeat;
  background-position: center;
  top: 0;
